import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import "./MyOrders.css";
import constants from "../../Constants/constants";
import {
  TextField,
  Checkbox,
  Grid,
  Card,
  Button,
  IconButton,
  FormControlLabel,
  Switch,
} from "@material-ui/core";
import { Link } from "react-router-dom";

import TodayIcon from "@material-ui/icons/Today";
import Typography from "@material-ui/core/Typography";
import LocalShippingIcon from "@material-ui/icons/LocalShipping";
import ClassTwoToneIcon from "@material-ui/icons/ClassTwoTone";
import { API, Auth } from "aws-amplify";
import Spinner from "../UI/Spinner";
import CardContent from "@material-ui/core/CardContent";
const useStyles = makeStyles((theme) => ({
  "@keyframes blinker": {
    from: { opacity: 1 },
    to: { opacity: 0.2, color: "#3f51b5", fontWeight: "400" },
  },
  button: {
    marginBottom: theme.spacing(1),
  },
  allocationButton: {
    animationName: "$blinker",
    animationDuration: "0.7s",
    animationTimingFunction: "linear",
    animationIterationCount: "infinite",
    marginBottom: theme.spacing(1),
    marginRight: theme.spacing(2),
  },
  title: {
    fontSize: 20,
    height: 50,
    padding: 10,
    paddingLeft: 55,
    borderBottomStyle: "solid",
    borderWidth: "1px",
    borderRadius: "5px",
  },
}));

const MyOrders = () => {
  const classes = useStyles();
  const [activeOrders, setActiveOrders] = useState([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    async function putValues() {
      var currentUser = await Auth.currentUserInfo();
      var currentUsername = currentUser.username;
      //currentUser.username
      var temp = await API.get(
        "GoFlexeOrderPlacement",
        `/serviceorder?username=${currentUsername}`
      );
      console.log(temp);
      setActiveOrders(temp);
      setLoading(false);
    }
    putValues();
  }, []);

  if (activeOrders == "") {
    if (loading == true) {
      return (
        <div>
          <Spinner />
        </div>
      );
    } else {
      return (
        <div>
          <Typography
            style={{ fontSize: 20, height: 50, padding: 10, paddingLeft: 55 }}
          >
            You have not accepted any orders yet.
          </Typography>
        </div>
      );
    }
  } else {
    return (
      <div>
        {/* <Typography className={classes.title} gutterBottom style={{}}>
                                    My Active Orders
                    </Typography>   */}
        <section className="">
          <div>
            <Grid
              container
              spacing={10}
              style={{ paddingTop: 20, paddingBottom: 30, paddingLeft: 70 }}
            >
              {constants.orders.map((orders) => {
                return (
                  <Grid item xs={12} sm={4}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          //checked={state.checkedB}
                          //onChange={handleChange}
                          name={orders.name}
                          color="primary"
                        />
                      }
                      label={orders.name}
                    />
                  </Grid>
                );
              })}
            </Grid>
          </div>
          {activeOrders.map((eachOrder) => (
            <div>
              <Card
                style={{
                  margin: 0,
                  marginTop: 20,
                }}
              >
                <CardContent
                  style={{
                    marginTop: 0,
                    padding: 16,
                  }}
                >
                  <Grid container spacing={5}>
                    <Grid item xs={2}>
                      <figure
                        style={{
                          marginTop: 0,
                          marginBottom: 0,
                          padding: 10,
                        }}
                      >
                        <img
                          alt="truck"
                          src="data:image/svg+xml;base64,"
                        />
                      </figure>
                    </Grid>

                    <Grid
                      item
                      xs={3}
                      style={{
                        marginTop: 15,
                        marginBottom: 20,
                      }}
                    >
                      {/* <table >
                      <tr
                        style={{
                          marginBottom: 25,
                          marginRight: 50,
                        }}
                      >
                        <th scope="row">User Id : </th>
                        <td>
                          <td>{eachOrder.displayId}</td>
                        </td>
                      </tr>
                      <tr>
                        <th scope="row">User Id : </th>
                        <td>
                          <td>{eachOrder.displayId}</td>
                        </td>
                      </tr>  </table> */}
                      <h6
                        style={{
                          marginBottom: 25,
                          marginRight: 50,
                        }}
                      >
                        Order No. {}
                        <span>{eachOrder.displayId}</span>
                      </h6>
                      <h6>
                        Pickup Date :{" "}
                        {String(
                          new Date(eachOrder.estimatedPickupDate)
                        ).substring(0, 11)}
                      </h6>
                    </Grid>
                    <Grid
                      item
                      xs={4}
                      style={{
                        marginTop: 15,
                        marginBottom: 20,
                      }}
                    >
                      <h6
                        style={{
                          marginBottom: 25,
                        }}
                      >
                        Order Id :{eachOrder.ServiceOrderId.substring(0, 8)}
                      </h6>
                      <h6>
                        Delivery Date :{" "}
                        {String(
                          new Date(eachOrder.estimatedDeliveryDate)
                        ).substring(0, 11)}
                      </h6>
                    </Grid>

                    <Grid item xs={2}>
                      <Button
                        style={{
                          maxWidth: "150px",
                          minWidth: "150px",
                          maxHeight: "50px",
                          marginTop: 5,
                          marginBottom: 7,
                        }}
                        component={Link}
                        to={`assignment/${eachOrder.ServiceOrderId}/${eachOrder.customerOrderId}`}
                        variant="contained"
                        color="default"
                        startIcon={<LocalShippingIcon />}
                        // component={Link}
                        // to="/track"
                      >
                        Assign
                      </Button>
                      <Button
                        style={{
                          minWidth: "150px",
                          maxWidth: "150px",
                          maxHeight: "50px",
                          marginTop: 7,
                          marginBottom: 5,
                        }}
                        component={Link}
                        to={`order/${eachOrder.customerOrderId}`}
                        variant="contained"
                        color="default"
                        // className={classes.allocationButton}
                        startIcon={<ClassTwoToneIcon />}
                      >
                        Details
                      </Button>
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </div>
          ))}
        </section>
      </div>
    );
  }
};

export default MyOrders;
